.detail-page {
  min-height: 100%;
  position: relative;
  padding-bottom: 80px;
  &__slider {
    height: 280px;
    position: relative;
    overflow: hidden;
    z-index: 1;
    @include clr;
    .slick-list,
    .slick-track,
    .slick-track div {
      height: 100%; }
    .slick-track div {
      float: left;
      @include cover; }
    .slick-slide {
      position: relative; }
    .slick-dots {
      position: absolute;
      bottom: 15px;
      left: 50%;
      transform: translateX(-50%);
      background-color: rgba(#fff, .5);
      height: 24px;
      z-index: 9999;
      border-radius: 12px;
      padding: 0 7px;
      li {
        display: inline-block;
        vertical-align: top;
        line-height: 0;
        margin-top: 4px;
        &:not(:last-child) {
          margin-right: 12px; }
        &.slick-active button {
          background-color: $primary; } }
      button {
        width: 16px;
        height: 16px;
        border-radius: 50%;
        background-color: #fff;
        font-size: 0;
        transition: background-color .5s; } } }
  &__info {
    margin-top: 20px;
    width: 100%;
    @include clr; }
  &__label {
    float: left;
    font-size: 18px;
    @include f-demi;
    width: 140px;
    box-sizing: border-box;
    padding-right: 15px;
    background-color: $primary;
    text-align: right;
    line-height: 28px;
    &_gray {
      background-color: #ccc; }
    &_s {
      width: 88px; } }
  &__date {
    @include reg;
    font-size: 16px;
    float: right;
    padding-right: 25px;
    line-height: 28px; }
  &__content {
    padding: 0 20px; }
  &__title {
    @include f-demi;
    font-size: 30px;
    margin-top: 30px;
    margin-bottom: 20px; }
  &__text {
    font-size: 18px;
    @include reg; }
  &__btn-container {
    text-align: center;
    margin: 30px auto 100px;
    width: 280px;
    .button:not(:last-child) {
      margin-bottom: 20px; } }

  .detail-page__slider .detail-page__video-btn {
    position: absolute;
    top: calc(50% - 50px);
    left: calc(50% - 50px);
    background-image: url(../img/video-btn.png);
    width: 100px;
    height: 100px;
    @include cover; }

  .yw-socials {
    position: absolute;
    bottom: 0; } }

.yw-socials_yellow {
  background-color: $primary;
  .icon {
    fill: #000; } }
