.checkbox {
	display: block;
	input[type="checkbox"] {
		position: absolute;
		opacity: 0;
		visibility: hidden;
		&:checked {
			+ .checkbox__block {
				background: svg-load('../../build/img/check.svg', fill=#fff) $primary center center no-repeat; }
			~ .type__icon_hover {
				opacity: 1; } } }

	&__block {
		position: relative;
		display: inline-block;
		height: 40px;
		width: 40px;
		border: 1px solid $primary;
		&_bold {
			border-width: 4px; }
		&_left {
			position: absolute;
			top: 0;
			left: 0; }
		&_right {
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			right: 20px; } }

	&__name {
		@include f-book;
		font-size: 26px;
		color: #fff;
		margin-left: 5px;
		margin-bottom: 7px; }

	&__img {
		content: '';
		position: absolute;
		font-size: 4px;
		top: 50%;
		left: 0;
		height: 6.5em;
		width: 5em;
		margin-top: -16px;
		background: center/cover no-repeat;
		@include gpu; } }

.checkbox_dark {
	input[type="checkbox"] {
		&:checked + .checkbox__block {
			background-color: $black; } }
	.checkbox__block {
		border-color: $black; } }

.checkbox_icon {
	position: relative;
	padding-left: 30px; }

.checkbox_mod {
	position: relative;
	input[type="checkbox"] {
		&:checked + .checkbox__block {
			background-color: $black;
			background-size: 16px 16px; } }
	.checkbox__block {
		width: 20px;
		height: 20px;
		float: right;
		margin-top: 1px;
		border-color: $black; } }

.checkbox_left {
	.checkbox__block {
		float: left;
		margin-right: 10px; } }
