.burger {
	display: block;
	background: none;
	position: relative;
	&.is-active {
		span {
			&:nth-child(1) {
				top: 50%;
				width: 0%;
				margin-left: 0; }
			&:nth-child(2) {
				transform: rotate(45deg); }
			&:nth-child(3) {
				transform: rotate(-45deg); }
			&:nth-child(4) {
				bottom: 50%;
				width: 0%;
				margin-left: 0; } } }
	span {
		display: block;
		position: absolute;
		height: 2px;
		width: 35px;
		background: $primary;
		opacity: 1;
		left: 50%;
		margin-left: -17px;
		transform: rotate(0deg);
		transition: .25s ease-in-out;
		&:nth-child(1) {
			top: 20px; }
		&:nth-child(2), &:nth-child(3) {
			top: 50%;
			margin-top: -1px; }
		&:nth-child(4) {
			bottom: 20px; } } }


