.nav {
	position: relative;
	.swiper-wrapper {
		width: 3000px;
		&_archive {
			width: 100%; } }
	.swiper-slide {
		width: auto; }
	&__line {
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translate3d(-50%,0,0);
		height: 8px;
		width: 80px;
		background: $primary;
		transition: width .4s ease; }
	&__slide {
		opacity: .7;
		font-size: 18px;
		height: 60px;
		line-height: 60px;
		margin: 0 12px;
		&:first-child {
			position: relative;
			&:before {
				content: '';
				width: 16px;
				height: 16px;
				position: absolute;
				background: $primary;
				left: -30px;
				top: 21px;
				border-radius: 8px;
				animation-duration: 1.5s;
				animation-iteration-count: infinite;
				animation-name: scroll; } }
		&.swiper-slide-active {
			opacity: 1;
			.nav__item {
				transform: scale(1.1) translate3d(0, -2px, 0); } } }
	&__item {
		@include f-demi;
		text-transform: uppercase;
		transition: transform .4s ease; } }

@keyframes scroll {
	0% {
		opacity: 1; }
	100% {
		opacity: 0;
		transform: translateX(-46px); } }
