$default-fonts: Helvetica, Arial, sans-serif;

@include font(Casper-Regular, Casper-Regular);
@mixin reg {
  font-family: 'Casper-Regular', $default-fonts; }
@include font(Casper-Bold, Casper-Bold);
@mixin bold {
  font-family: 'Casper-Bold', $default-fonts; }

@include font(FuturaPT-Demi, FuturaPT-Demi);
@mixin f-demi {
  font-family: 'FuturaPT-Demi', $default-fonts; }
@include font(FuturaPT-Bold, FuturaPT-Bold);
@mixin f-bold {
  font-family: 'FuturaPT-Bold', $default-fonts; }
@include font(FuturaPT-Book, FuturaPT-Book);
@mixin f-book {
   font-family: FuturaPT-Book, $default-fonts; }

html, body, .out {

  height: 100%; }

body {
  background: #FFF;
  //+css-lock(16, 18, 320, 1000)
  font-size: 18px;
  line-height: 1.3;
  @include reg;
  &.popup-open {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 100%;
    overflow: hidden; } }

a {
  text-decoration: none;
  color: inherit; }

.out {
  //position: relative
  padding-top: 60px;
 }  //overflow: hidden

.container {
  padding: 0 20px; }
