.np-header {
  padding: 5px 15px;
  box-shadow: 0 0 9px 0 rgba(0, 0, 0, 0.21);
  width: 100%;
  position: fixed;
  z-index: 100;
  top: 60px;
  background-color: #fff;
  @include clr;
  &__controlls {
    float: left; }
  &__checkbox {
    display: inline-block;
    vertical-align: top;
    line-height: 50px;
    &:not(:last-child) {
      margin-right: 10px; }
    &_event {
      input[type="checkbox"] + .checkbox__block {
        border-color: #ee5004; } }
    &_event {
      input[type="checkbox"]:checked + .checkbox__block {
        background-color: #ee5004; } }
    .checkbox__block {
      height: 25px;
      width: 25px;
      margin-top: 13px; }
    span {
      display: inline-block;
      vertical-align: top;
      font-size: 21px;
      @include reg; } }
  &__btn-container {
    float: right; } }
.np-content {
  padding: 10px;
  margin-top: 60px; }
.np-item {
  height: 350px;
  box-sizing: border-box;
  margin-bottom: 10px;
  &:before {
      content: '';
      position: absolute;
      width: calc(100% - 20px);
      height: inherit;
      background: linear-gradient(to top, rgba(0, 0, 0, .5), transparent); }
  @include cover;
  &__label {
    width: 75px;
    padding: 0 5px 3px;
    font-size: 18px;
    color: #000;
    @include f-demi;
    &_news {
      background-color: $primary; }
    &_event {
      background-color: #ee5004; } }
  &__content {
    padding: 160px 40px 0;
    z-index: 10;
    position: relative; }
  &__title {
    margin-top: 20px;
    font-size: 24px;
    line-height: 24px;
    color: #fff;
    @include f-demi; }
  &__date {
    font-size: 18px;
    color: #fff;
    margin-top: 15px;
    @include reg; } }
