.card {
	border: 1px solid rgb(219, 219, 219);
	border-top: 0;
	border-radius: 0 0 4px 4px;
	box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.06);
	&__slide {
		height: 290px;
		background: center center / cover no-repeat; }
	&__info {
		padding: 15px 50px; }
	&__name {
		font-size: 24px;
		color: #6c6c70;
		@include f-demi;
		margin-bottom: 15px; }
	&__subtitle {
		font-size: 24px;
		@include f-bold;
		text-transform: uppercase; }
	&__text {
		font-size: 18px;
		@include f-demi; }
	.swiper-pagination-bullet {
		width: 6px;
		height: 6px;
		background: $white;
		opacity: .4; }
	.swiper-pagination-bullet-active {
		background: $primary;
		opacity: 1; } }
