.email {
  position: relative;
  &__title {
    font-size: 45px;
    text-transform: uppercase;
    @include f-bold;
    color: $primary;
    margin-bottom: 30px; }
  &__body {
    padding: 35px;
    padding-top: 125px;
    padding-bottom: 70px;
    &_thnk {
      text-align: center; } }
  &__text {
    font-size: 28px;
    color: #fff;
    @include f-demi;
    margin-bottom: 15px;
    &_l {
      font-size: 34px;
      text-transform: uppercase;
      @include f-bold; }
    &_center {
      text-align: center; } }
  &__pic {
    width: 240px;
    height: 160px;
    margin: 0 auto;
    margin-bottom: 50px;
    img {
      object-fit: contain; } } }

.email-interests {
  width: 73%;
  margin: 0 auto;
  &__title {
    color: $primary;
    font-size: 36px;
    @include f-demi;
    width: 100%; } }

.email-checkboxes {
  margin-top: 25px;
  display: flex;
  flex-wrap: wrap;
  &__item {
    width: 49%;
    margin-bottom: 15px;
    display: flex;
    align-items: center; } }

.email-input {
  margin-top: 25px;
  width: 100%;
  height: 60px;
  padding: 5px;
  padding-left: 15px;
  background-color: rgba(#fff, .5);
  border-top-right-radius: 30px;
  border-bottom-right-radius: 30px;
  display: flex;

  &__btn {
    margin-left: auto;
    width: auto; }
  &__input {
    flex: 1 1 auto;
    background-color: transparent;
    font-size: 18px;
    color: #fff;
    @include reg;
    &::placeholder {
      font-size: 18px;
      color: #fff;
      @include reg; } }
  &.error {
    background-color: #ff6e6e;
    .email-input__input {
      &::-webkit-input-placeholder {
        color: #cc3333; }
      &::-moz-input-placeholder {
        color: #cc3333; }
      &::-ms-input-placeholder {
        color: #cc3333; } } } }

.email-delay-text {
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: #4c4c4c;
  padding: 60px 30px;
  color: #fff;
  width: 100%;
  height: 245px;
  @include reg;
  font-size: 21px; }
