.btn-close {
	display: inline-block;
	background: none;
	@include vertical;
	padding: 10px;
	.icon {
		vertical-align: middle;
		font-size: 30px; } }

.button {
	display: inline-block;
	height: 50px;
	line-height: 50px;
	padding: 0 18px;
	border: 1px solid $black;
	border-radius: 50px;
	background: none;
	color: $black;
	font-size: 18px;
	text-align: center;
	text-transform: uppercase;
	&_text-small {
		text-transform: none; }
	.icon {
		vertical-align: middle;
		margin-top: -5px;
		margin-right: 10px; } }

.button_sm {
	width: 200px; }

.button_sm {
	width: 150px; }

.button_lg {
	width: 230px; }

.button_xxl {
	width: 280px;
	vertical-align: top; }

.button_xl {
	width: 300px;
	font-size: 22px; }

.button_icon {
	vertical-align: top;
	width: 100px;
	.icon {
		margin-right: 0;
		font-size: 26px; } }

.button_icon-360 {
	background: #fff url('../img/i-tour.png') center center / 36px 22px no-repeat; }

.button_white {
	border-color: $white;
	background: $white; }

.button_yellow {
	border-color: $primary;
	background: $primary; }

.button_tr-white {
	background-color: transparent;
	color: $white;
	border-color: $white; }

.button_dark {
	background-color: rgba(0,0,0,.8);
	color: $primary;
	border-color: $primary; }

.button_purple {
	border-color: $primary;
	color: $primary;
	background-color: rgba(#36008e, .8); }

.button_black {
	background-color: rgba(0,0,0,.8);
	color: $white;
	border-color: rgba(0,0,0,.8); }

.input {
	&__main {
		width: 100%;
		height: 40px;
		padding: 0 20px;
		border: 1px solid $grayLight;
		border-radius: 3px;
		font-size: 18px;
		@include placeholder {
			color: $black; } }
	textarea.input__main {
		height: 200px;
		padding-top: 10px;
		padding-bottom: 10px;
		border: 0;
		color: $black;
		resize: vertical;
		@include placeholder {
			color: $black;
			font-size: 18px; } }
	&__label {
		display: block;
		margin: 2px 0 0 20px;
		font-size: 18px; }
	&__text {
		margin-left: 10px; }
	&_white {
		.input__main {
			color: #fff;
			background: rgba(255, 255, 255, 0.1);
			border-radius: 0;
			border: 0;
			@include placeholder {
				color: rgba(255,255,255,.5); } }
		textarea.input__main {
			color: $white;
			@include placeholder {
				color: $white; } }
		.input__label {
			color: #fff; } }
	&__descr {
		margin-top: 30px;
		margin-left: 20px; } }

.input_inline {
	display: inline-block; }

.input_additional {
	.input {
		&__main {
			width: 90px;
			padding: 0 10px; }
		&__text {
			display: inline-block;
			vertical-align: middle; } } }

.input_lg {
	.input__main {
		height: 70px;
		font-size: 30px; } }

.input_op {
	.input__main {
		border-radius: 0;
		border: 0;
		background: rgba($white, .4); }
	.ms-choice {
		border: 0;
		background: rgba($white, .4);
		font-size: 30px;
		height: 70px;
		line-height: 70px;
		> span {
			padding: 0 20px; } }
	.ms-parent {
		border: 0;
		font-size: 24px; }
	.ms-drop.bottom {
		box-shadow: none;
		background: #fae067;
		border: none;
		padding: 0 20px;
		input {
			display: none; } } }

.form {
	&__block {
		margin-bottom: 15px;
		padding: 0 10px 20px;
		&:not(:last-child) {
			border-bottom: 1px solid $grayLight; } }
	&__block_no-bd {
		&:not(:last-child) {
			border-bottom: 0; } }
	&__checkbox {
		padding-right: 150px;
		margin-bottom: 15px; }
	&__checkbox_l {
		padding-right: 0;
		span {
			@include bold; } }
	&__checkbox_subtype {
		margin-left: 30px;
		span {
			@include reg; } }
	&__checkbox-group {
		& + & {
			margin-top: 30px; } }
	&__field {
		margin-bottom: 26px;
		&:last-child {
			margin-bottom: 0; } }
	&__field_mt {
		margin-top: 15px; }
	&__field_center {
		text-align: center; }
	&__title {
		margin-bottom: 10px;
		@include f-bold;
		text-transform: uppercase; }
	&__title_mb {
		margin-bottom: 20px; }
	&__row {
		margin: 0 -15px;
		@include clr; }
	&__col {
		width: 50%;
		float: left;
		padding: 0 15px; }
	&__col_full {
		width: 100%; }
	&__col_center {
		text-align: center; }
	&__col_right {
		text-align: right; }
	&__inputs {
		display: flex;
		justify-content: space-between; }
	&__btns {
		text-align: center;
		.button {
			margin-right: 10px;
			&:last-child {
				margin-right: 0; } } }
	&__slider {
		margin-top: 30px;
		margin-bottom: 15px; } }

.js-select-multiple li span {
	margin-left: 10px; }

.ms-drop ul > li label {
	position: relative;
	z-index: 1; }

.js-select-multiple li::before {
	width: 18px;
	height: 18px;
	border: 1px solid #000; }

.js-select-multiple li.selected::before {
	background: svg-load('../../build/img/check.svg', fill=#fff) #000 center center no-repeat;
	background-size: 16px 16px; }

.btn-share {
	border: 1px solid #475993;
	text-align: center;
	outline: none;
	background-color: #fff;
	display: inline-block;
	overflow: hidden;
	text-transform: capitalize;
	cursor: pointer;
	position: relative;
	height: 50px;
	line-height: 50px;
	font-size: 18px;
	border-radius: 25px;
	width: 130px;
	margin-bottom: 20px;
	@include reg;
	transition: all .25s ease-in-out;
	&.is-shared {
		background-color: #475993;
		.btn-share__icon {
			left: 18%;
			background-color: #fff;
			i {
				path {
					fill: #475993; } } } }
	&__icon {
		border-radius: 50%;
		background-color: #475993;
		position: absolute;
		top: 50%;
		transform: translate(-50%, -50%);
		left: 82%;
		transition: all .25s ease-in-out;
		width: 40px;
		height: 40px;
		&:before,
		&:after {
			content: 'Share it';
			@include reg;
			font-size: 16px;
			position: relative;
			width: 60px;
			display: block; }
		&:before {
			color: #475993;
			left: -69px;
			top: -5px; }
		&:after {
			color: #fff;
			left: 45px;
			top: -55px; }
		i {
			width: 15px;
			height: 30px;
			@include vcenter; }
		svg {
			width: 100%;
			height: 100%; } } }

input[type="tel"]:focus {
	@include placeholder {
		color: transparent; } }
