.region {
	height: 80px;
	margin: 2px 0;
	padding: 0 20px;
	background: #1d1d1d;
	color: #fff;
	@include f-demi;
	font-size: 24px;
	line-height: 80px;
	text-transform: uppercase;
	@include r(375) {
		font-size: 20px; }
	&__checkbox {
		.checkbox {
			&__block {
				float: right;
				margin-top: 18px; } } } }

.regions {
	margin-bottom: 44px; }
